<template>
  <div class="carousel-overlay" @click="handleOutsideClick">
    <div
      class="carousel-wrapper"
      ref="carousel"
      :class="{ 'fade-in': visible }"
    >
      <div
        class="image-wrapper"
        v-for="(src, idx) in images"
        :key="`image-${idx}`"
      >
        <img :src="src" />
      </div>
      <button class="prev" @click="move(-1)" :disabled="isAnimating">
        prev
      </button>
      <button class="next" @click="move(1)" :disabled="isAnimating">
        next
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  onBeforeUnmount,
  onMounted,
  reactive,
  ref,
  toRefs,
} from 'vue';

export default defineComponent({
  props: {
    selectedIndex: {
      type: Number,
      default: 0,
    },
    images: {
      type: Array,
      default: () => [],
    },
    visible: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const state = reactive({
      scrollPosition: 0,
      touchStartX: 0,
      touchCurrentX: 0,
      isAnimating: false,
      scrollStart: 0,
    });
    const carousel = ref<HTMLElement | null>(null);

    onMounted(() => {
      moveToSelectedImage();
      setEventListener();
      window.addEventListener('resize', handleResize);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', handleResize);
    });

    //       
    const handleResize = () => {
      if (carousel.value) {
        const imageWidth = carousel.value.offsetWidth;
        carousel.value.scrollLeft =
          Math.round(carousel.value.scrollLeft / imageWidth) * imageWidth;
      }
    };

    //        .
    const moveToSelectedImage = () => {
      if (carousel.value) {
        const imageWidth = carousel.value.offsetWidth; // carousel-wrapper width
        state.scrollPosition = imageWidth * props.selectedIndex; //     
        carousel.value.scrollLeft = state.scrollPosition;
      }
    };

    //   
    const setEventListener = () => {
      const carouselElement = carousel.value;
      if (carouselElement) {
        carouselElement.addEventListener('touchstart', onTouchStart);
        carouselElement.addEventListener('touchmove', onTouchMove);
        carouselElement.addEventListener('touchend', onTouchEnd);
      }
    };

    const onTouchStart = (event: TouchEvent) => {
      if (state.isAnimating) return;
      state.touchStartX = event.touches[0].clientX;
      state.scrollStart = carousel.value ? carousel.value.scrollLeft : 0;
    };

    const onTouchMove = (event: TouchEvent) => {
      if (state.isAnimating) return;
      state.touchCurrentX = event.touches[0].clientX;
      const dx = state.touchStartX - state.touchCurrentX;
      if (carousel.value) {
        carousel.value.scrollLeft = state.scrollStart + dx;
      }
    };

    const onTouchEnd = () => {
      if (state.isAnimating) return;
      const dx = state.touchStartX - state.touchCurrentX;

      if (Math.abs(dx) > 30) {
        const direction = dx > 0 ? 1 : -1;
        move(direction);
      } else {
        if (carousel.value) {
          carousel.value.scrollTo({
            left: state.scrollStart,
            behavior: 'smooth',
          });
        }
      }
    };

    // carousel-wrapper     
    const handleOutsideClick = (event: Event) => {
      event.stopPropagation();

      if (carousel.value && !carousel.value.contains(event.target as Node)) {
        emit('outside-click');
      }
    };

    const delayAnimating = () => {
      setTimeout(() => {
        state.isAnimating = false;
      }, 300);
    };

    // overflow-x   
    const move = (direction: number) => {
      if (state.isAnimating) return;

      if (carousel.value && props.images.length > 0) {
        state.isAnimating = true; //   
        const imageWidth = carousel.value.offsetWidth;
        const numImages = props.images.length;
        let newScrollPosition =
          carousel.value.scrollLeft + imageWidth * direction;

        //    'prev'      
        if (direction === -1 && carousel.value.scrollLeft === 0) {
          newScrollPosition = imageWidth * (numImages - 1);
          carousel.value.scrollLeft = newScrollPosition; //   
          delayAnimating();
        }
        //   'next'       
        else if (
          direction === 1 &&
          carousel.value.scrollLeft >= imageWidth * (numImages - 1)
        ) {
          newScrollPosition = 0;
          carousel.value.scrollLeft = newScrollPosition; //   
          delayAnimating();
        }
        //    
        else {
          customScrollBehavior(carousel.value, newScrollPosition, 850);
        }
      }
    };

    // [scroll-behavior: smooth]  duration      
    const customScrollBehavior = (
      element: HTMLElement,
      endPosition: number,
      duration: number,
    ) => {
      //   
      let startTime: number | null = null;

      //   
      const startScrollLeft = element.scrollLeft;
      //    
      const distanceToScroll = endPosition - startScrollLeft;

      // Easing :  ,  , ,      
      const easeInOutQuad = (
        currentTime: number,
        startValue: number,
        changeInValue: number,
        duration: number,
      ) => {
        //      . (  /  )
        currentTime /= duration / 2;

        //  
        if (currentTime < 1)
          /*
          changeInValue / 2 =>   .      
          currentTime * currentTime =>    
        */
          return (changeInValue / 2) * currentTime * currentTime + startValue;

        //  
        currentTime--;
        return (
          (-changeInValue / 2) * (currentTime * (currentTime - 2) - 1) +
          startValue
        );
      };

      const animation = (currentTime: number) => {
        if (!startTime) startTime = currentTime;
        const timeElapsed = currentTime - startTime;
        const newScrollPosition = easeInOutQuad(
          timeElapsed,
          startScrollLeft,
          distanceToScroll,
          duration,
        );
        element.scrollLeft = newScrollPosition;

        // duration     
        if (timeElapsed < duration) {
          requestAnimationFrame(animation);
        } else {
          element.scrollLeft = endPosition; //    
          delayAnimating();
        }
      };

      requestAnimationFrame(animation);
    };

    return {
      ...toRefs(state),
      carousel,
      handleOutsideClick,
      move,
    };
  },
});
</script>

<style lang="scss" scoped>
.fade-in {
  animation: fadeIn 1s forwards;
  -webkit-animation: fadeIn 1s forwards;
}

.carousel-overlay {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;

  .carousel-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    overflow-x: auto;

    .image-wrapper {
      img {
        // %           
        max-width: 100vw; //     
        max-height: 100vh;
        object-fit: cover;
      }
    }

    button {
      position: fixed;
    }

    .prev {
      left: 0;
    }

    .next {
      right: 0;
    }
  }
}
</style>
