      </div>
      <div class="bus-info">
        <div class="transport"></div>
        <ul>
          <li>
            <span class="text-highlight" style="color: #0072bc"
              >()</span
            >
            : 272, 470, 601, 606, 607, 672, 700, 707, 710, 750A, 750B, 742
            (    )
          </li>
          <li>
            <span class="text-highlight" style="color: #197b30"
              >()</span
            >
            : 6714, 7017, 7024, 7737 (    )
