    }
    .road-name-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .road-name {
        font-weight: 600;
        opacity: 0.7;
      }
      .reset-area {
        display: flex;
        align-items: center;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;

        img {
          width: 25px;
          height: 25px;
          margin-bottom: 13px;
        }

        .reset {
          font-size: 0.7rem;
          font-weight: 600;
        }
      }
      .reset-area.active {
        // animation: bounce 500ms;
        // -webkit-animation: bounce 500ms;
      }
    }
  }

  .map {
