          height: 20px;
          margin-right: 5px;
        }
      }

      .map-name {
        color: #000000;
      }
    }

    a:first-child {
      margin-left: 0;
    }

    a:last-child {
      margin-right: 0;
    }

    a::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(255, 165, 0, 0.5);
      border-radius: 10px;
      opacity: 0;
      -webkit-transition: opacity 0.3s;
      transition: opacity 0.3s;
    }

    a:active::before {
      opacity: 1;
    }
  }

  .public-transport {
    .shuttle-bus {
      .sub {
        font-size: 0.8rem;
        font-weight: 500;
      }
    }
  }

  .transport {
    text-align: left;
    font-size: 0.95rem;
    font-weight: 600;
    padding: 20px 0 0 20px;
    // color: rgb(230, 140, 0);
  }

  ul {
    padding: 0 30px 0 40px;
  }

  ul > li {
    text-align: left;
    font-size: 0.95rem;
    line-height: 1.5rem;

    .text-highlight {
      font-weight: 600;
    }
  }
}
</style>
